.container {
	padding: 0 1em;

	> div {
		outline: 1px solid red;
	}

	@include media($desktop) {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}

/*logo*/
.site-branding {
	width: 19rem;
	float: left;
@include clearfix;
	@include media($medium-screen) {
		width: 30em;
	}


	@include media($desktop) {
		flex: none;
	}
}

.topnav {
	float: right;

	@include media($desktop) {
		flex-grow: 2;
		margin: $base-spacing;
	}
}

.simpleSearch {
	clear: both;

	@include media($medium-screen) {
		width: 267px;
		float: right;
		clear: right;
	}
}

.logged-in .navbar {
	top: 57px;

	@include media($large-screen) {
		top: 28px;
	}
}

.navbar {
	background-color: $header-background-color;

	// {{{
	z-index: 998;
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	transition: $base-transition;

	/*height: 200px;*/
	box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.5);
	padding-bottom: .5rem;

	.site-branding__logo img {
		/*width: 60px;*/
	}

	&.sticky {
		/*height: 100px;*/

		@include media($medium-screen) {
			.simpleSearch, .topnav {
				margin: 0;
				transition: $base-transition;
			}
		}


		.l-region--navigation {
			margin: 0;
			transition: $base-transition;
		}

		.site-branding__logo img {
			transition: $base-transition;
			width: 40px;
		}

		a.site-branding__name {
			transition: $base-transition;
			font-size: 1.9em;
		}

		h2.site-branding__slogan {
			transition: $base-transition;
			font-size: 1.5em;
		}
	}

}


.topnav span {
	float: right;
}

.sf-accordion-toggle {
	position: absolute;
	top: 10px;
	right: 10px;
}

.sf-accordion-toggle span {
	display: none;
}

.sf-accordion-toggle a {
	display: inline-block;
	font: normal normal normal 14px/1 FontAwesome;
	font-size: 2em;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;

	&:before {
		content: "\f0c9";
	}
}

ul#superfish-1-accordion {
	width: 204px;
	float: right;
	background: #fff;
	padding: .5em;
	border: 1px solid #ccc;
}

