.tabs {
	border: $base-border;
	border-width: 1px 0;
	margin-bottom: $base-spacing;
	background: #fff;
	li {
		display: inline-block;
		margin-right: $small-spacing;
		font-family: $sans-serif;
		text-transform: uppercase;
		font-size: .7em ;
		font-weight: 200;
	}
}

